<template>
  <view class="container">
    <!-- ÊêúÁ¥¢Ë°®ÂçïËßÜÂõæ -->
    <view v-if="!showResults" class="form-container">
      <view class="input-group">
        <text class="label">ÂïÜÂìÅÂêçÁß∞</text>
        <input
          class="input"
          v-model="query"
          placeholder="ËØ∑ËæìÂÖ•ÂïÜÂìÅÂêçÁß∞, Â¶Ç: ÊâãÊú∫"
          type="text"
        />
      </view>
      <view class="input-group">
        <text class="label">ÊúÄÈ´ò‰ª∑Ê†º</text>
        <input
          class="input"
          v-model="maxPrice"
          placeholder="ËØ∑ËæìÂÖ•ÊúÄÈ´ò‰ª∑Ê†º"
          type="number"
        />
      </view>
      <view class="input-group">
        <text class="label">ÊúÄ‰Ωé‰ª∑Ê†º</text>
        <input
          class="input"
          v-model="minPrice"
          placeholder="ËØ∑ËæìÂÖ•ÊúÄ‰Ωé‰ª∑Ê†º"
          type="number"
        />
      </view>
      <button class="submit-btn" @click="getAdvice" :disabled="loading">
        {{ loading ? "ÂàÜÊûê‰∏≠..." : "Ëé∑ÂèñÂª∫ËÆÆ" }}
      </button>
      <!-- ÊèêÁ§∫‰ø°ÊÅØ -->
      <view class="tips-container">
        <view class="tip-item">
          <uni-icons type="info" size="14" color="#909399"></uni-icons>
          <text class="tip-text">
            Âª∫ËÆÆÊúÄ‰Ωé‰ª∑ÂíåÊúÄÈ´ò‰ª∑Â∑ÆË∑ù‰∏çË¶ÅÂ§™Â§ßÔºåÂê¶ÂàôÂèØËÉΩ‰ºöÂΩ±ÂìçÂàÜÊûêÁªìÊûú
          </text>
        </view>
        <view class="tip-item">
          <uni-icons type="info" size="14" color="#909399"></uni-icons>
          <text class="tip-text">
            Â¶ÇÊûú‰∏ÄÊ¨°Ê≤°ÊúâËé∑ÂèñÂà∞ÁªìÊûúÂ∞±Â∞ùËØï‰øÆÊîπ‰ª∑Ê†ºÊàñËÄÖÈáçÊñ∞Ëé∑Âèñ
          </text>
        </view>
      </view>
    </view>

    <!-- ÁªìÊûúÂ±ïÁ§∫ËßÜÂõæ -->
    <view v-else class="results-view">
      <!-- È°∂ÈÉ®ÂØºËà™Ê†è -->
      <view class="nav-bar">
        <button class="back-btn" @click="showResults = false">
          <uni-icons type="back" size="10"></uni-icons>
        </button>
        <text class="page-title">ÂïÜÂìÅÊé®Ëçê</text>
      </view>
      <!-- ÂÜÖÂÆπÂÆπÂô® -->
      <view class="content-container">
        <scroll-view
          scroll-y
          class="result-list"
          :style="{ height: scrollHeight + 'px' }"
        >
          <view
            v-for="(item, index) in parsedResults"
            :key="index"
            class="result-card"
          >
            <!-- ÂïÜÂìÅÂêçÁß∞ -->
            <view class="product-name">{{ item.name }}</view>

            <!-- ‰ª∑Ê†º -->
            <view class="price">¬•{{ item.price }}</view>

            <!-- Ë¥≠‰π∞Âª∫ËÆÆ -->
            <view class="worth-info">
              <text class="channel">{{ item.channel }}</text>
              <text class="separator">-</text>
              <text class="worth">{{ item.worth }}</text>
            </view>
          </view>
        </scroll-view>
      </view>
    </view>
  </view>
</template>

<script setup>
import { ref, computed, onMounted, onUnmounted } from "vue";
import { getShoppingAdvice } from "@/api/search.js"; // ËØ∑Á°Æ‰øùAPIÊñπÊ≥ïÊ≠£Á°ÆÂºïÂÖ•

// ÂÆö‰πâË°®ÂçïËæìÂÖ•ÂèòÈáè
const query = ref("");
const minPrice = ref("");
const maxPrice = ref("");

// Áä∂ÊÄÅÂèòÈáè
const loading = ref(false);
const result = ref(null);
const showResults = ref(false);
let loadingTimer = null;

// Âú® script setup ‰∏≠Ê∑ªÂä†‰ª•‰∏ã‰ª£Á†Å
const userLocation = ref("");

// Ëé∑ÂèñÁî®Êà∑Âú∞ÂùÄÁöÑÊñπÊ≥ï
const getLocation = () => {
  return new Promise((resolve, reject) => {
    uni.getLocation({
      type: "gcj02",
      success: async (res) => {
        try {
          // ‰ΩøÁî®ÂíåÈ£éÂ§©Ê∞îAPIÁöÑÂüéÂ∏ÇÊü•ËØ¢Êé•Âè£Ëé∑Âèñ‰ΩçÁΩÆID
          const locationRes = await uni.request({
            url: `https://geoapi.qweather.com/v2/city/lookup`,
            method: "GET",
            data: {
              location: `${res.longitude},${res.latitude}`,
              key: "7fdcb07d68dc4296bd06970b643ec23a",
            },
          });

          if (
            locationRes.statusCode === 200 &&
            locationRes.data.code === "200"
          ) {
            const cityData = locationRes.data.location[0];
            userLocation.value = cityData.id;
            resolve(cityData.name);
          } else {
            reject(new Error("Ëé∑ÂèñÂüéÂ∏Ç‰ø°ÊÅØÂ§±Ë¥•"));
          }
        } catch (error) {
          reject(error);
        }
      },
      fail: (err) => {
        if (err.errMsg.includes("auth deny")) {
          uni.showModal({
            title: "ÊèêÁ§∫",
            content:
              "ÈúÄË¶ÅËé∑ÂèñÊÇ®ÁöÑ‰ΩçÁΩÆÊâçËÉΩ‰∏∫ÊÇ®Êé®ËçêÈôÑËøëÁöÑÂïÜÂìÅÔºåÊòØÂê¶ÊâìÂºÄËÆæÁΩÆÈ°µÈù¢ÈáçÊñ∞ÊéàÊùÉÔºü",
            success: (res) => {
              if (res.confirm) {
                uni.openSetting();
              }
            },
          });
        }
        reject(err);
      },
    });
  });
};

// Ë∞ÉÁî®APIËé∑ÂèñÂª∫ËÆÆÔºåÂπ∂Ëß£ÊûêËøîÂõûÊï∞ÊçÆ
const getAdvice = async () => {
  // ‰ª∑Ê†ºÊ†°È™åÔºöÁ°Æ‰øùÊúÄ‰Ωé‰ª∑‰∏çÂ§ß‰∫éÊúÄÈ´ò‰ª∑
  if (Number(minPrice.value) > Number(maxPrice.value)) {
    uni.showToast({
      title: "ÊúÄ‰Ωé‰ª∑Ê†º‰∏çËÉΩÂ§ß‰∫éÊúÄÈ´ò‰ª∑Ê†º",
      icon: "none",
      duration: 2000,
    });
    return;
  }

  try {
    loading.value = true;

    // Ëé∑Âèñ‰ΩçÁΩÆ‰ø°ÊÅØ
    let cityName = "";
    if (!userLocation.value) {
      try {
        cityName = await getLocation();
      } catch (error) {
        console.warn("Ëé∑Âèñ‰ΩçÁΩÆÂ§±Ë¥•:", error);
        // Ëé∑Âèñ‰ΩçÁΩÆÂ§±Ë¥•Êó∂‰∏çÈòªÊ≠¢ÂêéÁª≠Êìç‰Ωú
      }
    }

    console.log("ÂèëËµ∑APIËØ∑Ê±Ç...", {
      query: query.value,
      minPrice: minPrice.value,
      maxPrice: maxPrice.value,
      location: cityName,
    });

    // ‰øÆÊ≠£ÂèÇÊï∞È°∫Â∫èÔºåÁ°Æ‰øù‰∏éAPIÂÆö‰πâ‰∏ÄËá¥
    const res = await getShoppingAdvice(
      query.value,
      maxPrice.value,
      minPrice.value,
      cityName // Ê∑ªÂä†‰ΩçÁΩÆÂèÇÊï∞
    );
    console.log("APIÂìçÂ∫îÁªìÊûúÔºö", res);

    // ‰øÆÊîπËß£ÊûêÈÄªËæë
    if (res) {
      result.value = { output: res }; // Áõ¥Êé•Â∞ÜËøîÂõûÁªìÊûúÂåÖË£ÖÊàêÈúÄË¶ÅÁöÑÊ†ºÂºè
    } else {
      result.value = {};
    }

    console.log("result.valueÁöÑÂÄºÔºö", result.value);
    console.log("Ëß£ÊûêÂêéÁöÑÁªìÊûúÔºö", parsedResults.value);

    if (parsedResults.value.length > 0) {
      showResults.value = true;
    } else {
      console.log("Ê≤°ÊúâËß£ÊûêÂà∞ÊúâÊïàÁªìÊûú");
      uni.showToast({
        title: "Êú™Ëé∑ÂèñÂà∞ÂïÜÂìÅÊé®Ëçê",
        icon: "none",
        duration: 2000,
      });
    }
  } catch (error) {
    console.error("ËØ∑Ê±ÇÂ§±Ë¥•Ôºö", error);
    uni.showToast({
      title: error.message || "Ëé∑ÂèñÂª∫ËÆÆÂ§±Ë¥•",
      icon: "none",
      duration: 2000,
    });
  } finally {
    loading.value = false;
  }
};

onUnmounted(() => {
  if (loadingTimer) {
    clearTimeout(loadingTimer);
    loadingTimer = null;
  }
});

// Ëß£ÊûêÂïÜÂìÅ‰ø°ÊÅØÁöÑ computed Â±ûÊÄß
const parsedResults = computed(() => {
  if (!result.value?.output) return [];
  try {
    const text = result.value.output;
    const products =
      text.match(/- üõçÔ∏è ÂïÜÂìÅÂêçÁß∞:[\s\S]*?(?=(?:- üõçÔ∏è ÂïÜÂìÅÂêçÁß∞:|$))/g) || [];

    const results = products
      .map((product) => {
        try {
          const nameMatch = product.match(/ÂïÜÂìÅÂêçÁß∞:\s*([^\n]+)/);
          const priceMatch = product.match(/‰ª∑Ê†º:\s*([\d.]+)/);
          const channelMatch = product.match(/Ë¥≠‰π∞Ê∏†ÈÅì:\s*([^\n]+)/);
          const worthMatch = product.match(/ÊòØÂê¶ÂÄºÂæóË¥≠‰π∞Ôºö\s*([^„ÄÇ\n]+)/);

          if (!nameMatch?.[1] || !priceMatch?.[1]) return null;

          const name = nameMatch[1]
            .trim()
            .replace(/\\n/g, "")
            .replace(/\[ÁÇπÂáª.*?\]/g, "")
            .replace(/üõçÔ∏è/g, "");
          const price = priceMatch[1].trim();
          const channel =
            channelMatch?.[1]
              ?.trim()
              .replace(/\\n/g, "")
              .replace(/\[ÁÇπÂáª.*?\]/g, "")
              .replace(/üõí/g, "") || "Âπ≥Âè∞Êú™Ê≥®Êòé";
          const worth =
            worthMatch?.[1]
              ?.trim()
              .replace(/\\n/g, "")
              .replace(/\[ÁÇπÂáª.*?\]/g, "")
              .replace(/„ÄÇ$/, "")
              .replace(/‚≠ê/g, "")
              .replace(/https?:\/\/[^\s)]+/g, "") || "ÊöÇÊó†ËØÑ‰º∞";

          return { name, price, channel, worth };
        } catch (error) {
          console.error("Ëß£ÊûêÈîôËØØÔºö", error);
          return null;
        }
      })
      .filter(Boolean);

    return results;
  } catch (error) {
    console.error("Ëß£ÊûêÈîôËØØÔºö", error);
    return [];
  }
});

// ËÆ°ÁÆóÊªöÂä®Âå∫ÂüüÁöÑÈ´òÂ∫¶
const scrollHeight = ref(0);
onMounted(async () => {
  try {
    await getLocation();
  } catch (error) {
    console.warn("ÂàùÂßãÂåñËé∑Âèñ‰ΩçÁΩÆÂ§±Ë¥•:", error);
  }

  // ÂéüÊúâÁöÑ onMounted ÈÄªËæë
  const systemInfo = uni.getSystemInfoSync();
  scrollHeight.value = systemInfo.windowHeight - 44 - 32;
});

// ÂÆö‰πâÂàÜ‰∫´ÁªôÊúãÂèãÂíåÂàÜ‰∫´Âà∞ÊúãÂèãÂúàÁöÑÊñπÊ≥ïÔºàÂ¶ÇÊûúÈúÄË¶ÅÔºâ
const onShareAppMessage = () => {
  return {
    title: "‰π∞‰ªÄ‰πà - AIÊô∫ËÉΩË¥≠Áâ©Âª∫ËÆÆ",
    path: "/pages/shopping/index",
    imageUrl: "", // Â¶ÇÊúâÂàÜ‰∫´ÂõæÁâáÂèØÂ°´ÂÜô
  };
};

const onShareTimeline = () => {
  return {
    title: "‰π∞‰ªÄ‰πà - AIÊô∫ËÉΩË¥≠Áâ©Âª∫ËÆÆ",
    query: "/pages/shopping/index",
    imageUrl: "", // Â¶ÇÊúâÂàÜ‰∫´ÂõæÁâáÂèØÂ°´ÂÜô
  };
};

defineExpose({
  onShareAppMessage,
  onShareTimeline,
});
</script>

<style lang="scss" scoped>
