  padding: 20px;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.1);
  margin-bottom: 12px;
}
.input-group {
  margin-bottom: 12px;
  .label {
    display: block;
    font-size: 13px;
    color: #333;
    margin-bottom: 4px;
  }
  .input {
    width: 100%;
    height: 36px;
    border: 1px solid #ddd;
    border-radius: 6px;
    padding: 0 12px;
    font-size: 14px;
    box-sizing: border-box;
    &:focus {
      border-color: #00bcd4;
    }
  }
}
.submit-btn {
  width: 100%;
  height: 40px;
  background: #00bcd4;
  color: #fff;
  border-radius: 6px;
  font-size: 15px;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  &:disabled {
    background: #ccc;
  }
  &:active:not(:disabled) {
    transform: scale(0.98);
  }
}
.results-view {
  min-height: 100vh;
  background-color: #f5f7fa;
  display: flex;
  flex-direction: column;
}
.nav-bar {
  position: sticky;
  top: 0;
  z-index: 100;
  height: 44px;
  background: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 16px;
  border-bottom: 1px solid #eee;
}
.back-btn {
  position: absolute;
  left: 16px;
  background: transparent;
  border: none;
  padding: 8px;
  display: flex;
