    margin-bottom: 8px;
    line-height: 1.4;
  }

  .price {
    font-size: 16px;
    color: #ff4d4f;
    margin-bottom: 8px;
  }

  .worth-info {
    font-size: 14px;
    color: #666;
    line-height: 1.4;

    .channel {
      color: #1890ff;
    }

    .separator {
      margin: 0 4px;
    }

    .worth {
      color: #666;
    }
  }
}
.tips-container {
  margin-top: 16px;
  padding: 12px;
  background: #f8f9fa;
  border-radius: 8px;
}
.tip-item {
  display: flex;
  align-items: flex-start;
  margin-bottom: 8px;
  padding: 4px 0;
  .uni-icons {
    margin-top: 3px;
  }
}
.tip-text {
  font-size: 13px;
